import logo from './logo.svg';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="App-text">
          <code>000000.000000</code>
        </p>
      </header>
    </div>
  );
}

function dec2binary(number) {
  var binary = "000000";
  if (number >= 32) {
    binary[0] = '1';
    number = number - 32;
  }
  if (number >= 16) {
    binary[1] = '1';
    number = number - 16;
  }
  if (number >= 8) {
    binary[2] = '1';
    number = number - 8;
  }
  if (number >= 4) {
    binary[3] = '1';
    number = number - 4;
  }
  if (number >= 2) {
    binary[4] = '1';
    number = number - 2;
  }
  if (number >= 1) {
    binary[5] = '1';
  }
  return binary
}

export default App;
